@charset "UTF-8";
.line {
  border-color: #dcdcdc; }

.text-main {
  color: #37c7a7; }

.text-yellow {
  color: #f7a500; }

.text-red {
  color: #f14948; }

.text-purple {
  color: #8352d1; }

.bg-main {
  background-color: #37c7a7; }

.bg-yellow {
  background-color: #f7a500; }

.bg-red {
  background-color: #f14948; }

.bg-purple {
  background-color: #8352d1; }

.text-shadow-red {
  text-shadow: 0.1rem 0.1rem 0.5rem #cf0039; }

.slick-list {
  padding-bottom: 0.5rem; }

.slick-slide {
  margin: 0 0.25rem; }

.slick-dots {
  display: flex;
  justify-content: center;
  margin: 0.5rem 0; }
  .slick-dots li {
    padding: 0.25rem;
    color: transparent;
    display: inline-block; }
    .slick-dots li:before {
      color: #000;
      content: "●";
      font-size: 1rem;
      display: inline-block; }
  .slick-dots li.slick-active:before {
    color: gray; }

html,
body {
  font-weight: 400;
  font-size: 12px; }

.nav_tab img {
  width: 35px; }

.nav_tab div {
  width: 20%;
  padding: 0.5rem;
  display: flex;
  height: 70px;
  flex-wrap: wrap;
  justify-content: center;
  align-content: space-between;
  position: relative; }
  .nav_tab div span {
    width: 100%;
    text-align: center; }
  .nav_tab div:after {
    content: " ";
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    border-right: 1px solid #dcdcdc;
    height: 35px; }
  .nav_tab div:last-of-type:after {
    border-color: white; }

.nav_fu .nav_fu_item .item_icon {
  width: 35px; }

.nav_fu .nav_fu_item:after {
  content: " ";
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  border-right: 1px solid #dcdcdc;
  height: 25px; }

.nav_fu .nav_fu_item:last-of-type:after {
  border-color: white; }

.sale .block_title {
  margin-top: calc(-1rem - 0px); }
  .sale .block_title img {
    width: 350px; }

.time {
  top: -0.5px; }
  .time .h,
  .time .m,
  .time .s {
    width: 22px;
    height: 22px;
    text-align: center;
    display: inline-block; }
